import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import WebApp from "@twa-dev/sdk";
import Clicker from "../../components/Clicker";
import Counter from "../../components/Counter";
import appStore from "../../store";
import { observer } from "mobx-react";
import ProgressBar from "../../components/ProgressBar";

const Home = observer(() => {
  const navigate = useNavigate();

  useEffect(() => {
    setTimeout(() => {
      appStore.showPage = true;
    }, 3000);
  }, []);

  return (
    <>
      <div className="relative overflow-hidden">
        {appStore.showPage && <div className="Klk3 Xhf3"></div>}
        <div className="Kh3x relative">
          <div className="min-h-[100vh] py-[16px]">
            {appStore.showPage && (
              <div className="mt-[42px] flex items-center justify-center GV3d">
                <div className="Bd2E"></div>
                <div className="UIn3">
                  <Counter start={appStore.balance} end={appStore.balance} />
                </div>
              </div>
            )}
            <div className="Km3x">
              <Clicker
                canIClickPlease={appStore.showPage ? true : false}
                sleep={false}
                funMode={false}
                clickValue={1}
                cooldown={0}
                handleClick={() => {
                  appStore.balance += 1;
                }}
              />
            </div>
            {appStore.showPage && (
              <div className="fixed bottom-0 left-[14px] right-[14px] mb-[112px] flex items-center justify-center">
                <div className="text-[22px] leading-[24px] flex items-center justify-center">
                  
                </div>
                <div className="F3xy text-white text-[17px] font-bold leading-[18px] ml-[6px]">
                  1000
                </div>
                <div className="ml-[14px] w-full">
                  <ProgressBar />
                </div>
              </div>
            )}

            {appStore.showPage && (
              <div className="fixed bottom-[30px] left-[14px] right-[14px]">
                <div className="flex justify-around rounded-[16px] bg-[rgba(255,255,255,.35)] transition ease duration-300">
                  <button
                    onClick={() => {
                      navigate("/frens");
                    }}
                    className="Kh3d"
                  >
                    <div className="text-[22px] flex items-center justify-center leading-[24px]">
                      
                    </div>
                    <div className="hcKs flex justify-center items-center text-[12px] font-[510] text-white mt-[2px]">
                      frens
                    </div>
                  </button>
                  <div className="h-[34px] w-[4px] mt-[16px] bg-[rgba(255,255,255,.16)]"></div>
                  <button
                    onClick={() => {
                      navigate("/earn");
                    }}
                    className="Kh3d"
                  >
                    <div className="text-[22px] flex items-center justify-center leading-[24px]">
                      
                    </div>
                    <div className="hcKs flex justify-center items-center text-[12px] font-[510] text-white mt-[2px]">
                      Earn
                    </div>
                  </button>
                  <div className="h-[34px] w-[4px] mt-[16px] bg-[rgba(255,255,255,.16)]"></div>
                  <button
                    onClick={() => {
                      navigate("/boost");
                    }}
                    className="Kh3d"
                  >
                    <div className="text-[22px] flex items-center justify-center leading-[24px]">
                      
                    </div>
                    <div className="hcKs flex justify-center items-center text-[12px] font-[510] text-white mt-[2px]">
                      Boost
                    </div>
                  </button>
                  <div className="h-[34px] w-[4px] mt-[16px] bg-[rgba(255,255,255,.16)]"></div>
                  <button
                    onClick={() => {
                      navigate("/wallet");
                    }}
                    className="Kh3d"
                  >
                    <div className="text-[22px] flex items-center justify-center leading-[24px]">
                      
                    </div>
                    <div className="hcKs flex justify-center items-center text-[12px] font-[510] text-white mt-[2px]">
                      Wallet
                    </div>
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </>
  );
});

export default Home;
