import React, { useEffect } from "react";
import AppRouter from "./router";
import WebApp from "@twa-dev/sdk";

function App() {
  useEffect(() => {
    WebApp.expand();
    WebApp.setHeaderColor("#000");
    WebApp.setBackgroundColor("#000");
  }, []);
  return (
    <>
      <AppRouter />
    </>
  );
}

export default App;
