import React, { useState } from "react";
import CountUp from "react-countup";

export default function Counter({ start, end }) {
  return (
    <CountUp
      duration={1}
      start={start}
      end={end}
      delay={0}
      separator=","
      easingFn={(t, b, c, d) => (c * t / d) * (3 / (2 + t / d)) + b}
      preserveValue={true}
    />
  );
}
